import React, { Component } from "react";
import ReactHTMLTableToExcel from "react-html-table-to-excel";
import { formatGram, getUserdata, localStoragedecryp } from "../../../../../components/helpers/function";
class LaporanExelPenjualanTukar extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  render() {
    let qty = 0;
    let berat = 0;
    let harga_beli = 0;
    let harga_gram = 0;
    let total_barang = 0;
    this.props.data.forEach((barang, index) => {
      barang.detail_barang.forEach((item, index) => {
        qty = qty + item.qty;
        berat = berat + item.berat;
        harga_beli = harga_beli + item.harga_beli;
        harga_gram = harga_gram + item.harga_gram
      });
      total_barang = total_barang + barang.detail_barang.length;
    });
    return (
      <>
        <ReactHTMLTableToExcel
          id="test-table-xls-button"
          className="btn btn-success btn-block"
          table="table-to-xls"
          filename="Laporan Batal Penjualan"
          sheet="Laporan Batal Penjualan"
          buttonText="Export Exel"
        />
        <table id="table-to-xls" style={{ display: "none" }}>
          <thead>
            <tr>
              <th colSpan="5" style={{ textAlign: "center" }}>
                {" "}
                LAPORAN BARANG TUKAR{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="5"> Periode </th>
            </tr>
            <tr>
              <th colSpan="5">
                {" "}
                {localStoragedecryp("tgl_laporan").length === 0
                  ? null
                  : JSON.parse(localStoragedecryp("tgl_laporan")).tgl_awal +
                    " s/d " +
                    JSON.parse(localStoragedecryp("tgl_laporan"))
                      .tgl_akhir}{" "}
              </th>
            </tr>
            <tr>
              <th colSpan="5"> TOKO :  {getUserdata().nama_toko} </th>
            </tr>
           
            <tr>
              <th colSpan="5"> ALAMAT :  {getUserdata().alamat_toko} </th>
            </tr>
            <tr></tr>
            <tr>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    NAMA BARANG
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    QTY
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    HARGA/GRAM
              </td>
              <td style={{ backgroundColor: "#E8E5E5", color: "#000" }}>
                    BERAT
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right" }}>
                    TOTAL HARGA
                  </td>
                </tr>
          </thead>
          {this.props.data.map((item, index) => (
            <>
              <tbody>
                {item.detail_barang.map((row, index) => (
                  <tr>
                    <td>
                      {row.nama_barang}
                    </td>
                    <td>
                      {row.qty}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.harga_gram.toLocaleString("kr-KO")}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {formatGram(row.berat,3)}
                    </td>
                    <td style={{textAlign: "right"}}>
                      {row.harga_beli.toLocaleString("kr-KO")}
                    </td>
                  </tr>
                ))}
              </tbody>
              <tfoot>
                <tr>
                  <td colSpan="1" style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "center" }}>
                    Total Barang : {item.detail_barang.length}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {item.detail_barang
                      .map((list) => list.qty)
                      .reduce((a, b) => a + b, 0)}{" "}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right"   }}>
                    {" "}
                    {item.detail_barang
                      .map((list) => list.harga_gram)
                      .reduce((a, b) => a + b, 0).toLocaleString("kr-KO")}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {formatGram(item.detail_barang
                      .map((list) => list.berat)
                      .reduce((a, b) => a + b, 0), 3)}{" "}
                  </td>
                
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {item.detail_barang
                      .map((list) => list.harga_beli)
                      .reduce((a, b) => a + b, 0).toLocaleString('kr-KO')}{" "}
                  </td>
                </tr>
              </tfoot>
            </>
          ))}
          <tr>
                  <td colSpan="1" style={{ backgroundColor: "#E8E5E5", color: "#000",  textAlign: "center" }}>
                    Grand Total Barang : {total_barang}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {qty}{" "}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000",textAlign: "right"   }}>
                    {" "}
                    {harga_gram.toLocaleString("kr-KO")}{" "}
                  </td>
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {formatGram(berat, 3)}{" "}
                  </td>
                
                  <td style={{ backgroundColor: "#E8E5E5", color: "#000", textAlign: "right"  }}>
                    {" "}
                    {harga_beli.toLocaleString("kr-KO")}{" "}
                  </td>
                </tr>
        </table>
      </>
    );
  }
}
export default LaporanExelPenjualanTukar;
